.Navbar{

 .logo{
  display: flex;
  align-items: center;
  color: white;
  a{
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-family: 'Times New Roman', Times, serif;
  }
 }
  padding: 0px 20px;
  margin: 0px;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  max-width: 100vw;
  ul{
    display: flex;
   justify-content: end;
    font-size: larger;
    list-style: none;
    li{
      justify-content: center;
      align-items: center;
      padding:0px 15px;
    }
    a{
      padding: 15px;
      text-decoration: none;
      color:rgb(0, 0, 0);
      transition: ease-in-out 0.3s ; 
      &:hover{
        background-color: rgba(70, 70, 70, 0.432);
        transition: ease-in-out 0.3s ; 
      }
    
    }
  }

}